.product-preview {
  position: relative;
  width: 292px;
  display: flex;
  flex-direction: column;
  padding: 16px 25px 25px;
  border: solid 1px $gray;
  background-color: $white;
  border-radius: 5px;

  &__label {
    position: absolute;
    top: -1px;
    right: -1px;
    min-width: 60px;
    padding: 13px 10px 14px;
    font-size: 19px;
    line-height: (23.16 / 19);
    color: $white;
    text-align: center;
    text-transform: uppercase;
    border-radius: 0 5px 0 5px;
    background-color: $blue;
  }

  &__image {
    width: 100%;
    height: 200px;
    text-align: center;

    img {
      display: inline-block;
      vertical-align: top;
      width: 100%;
      height: 100%;
      object-fit: scale-down;
    }
  }

  &__title {
    font-size: 15px;
    line-height: (32 / 15);
    color: $dark;
    font-weight: 700;

    a {
      transition: color 0.3s ease;

      @include hover {
        color: $blue;
      }
    }
  }

  &__text {
    margin-bottom: 18px;
    font-size: 14px;
    line-height: (22 / 14);
    color: $gray-ph;
  }

  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: auto;

    & > * {
      align-self: center;
    }
  }
  &__btn {
  }

  &__price {
    margin-left: 8px;
    font-size: 19px;
    line-height: (23.16 / 19);
    color: $dark-blue;
    text-align: right;

    &-old {
      display: block;
      font-size: 15px;
      line-height: (18.29 / 15);
      color: $gray;
      text-decoration: line-through;
    }
  }
}
